import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

export default{
	mixins: [common,globals],
	props: {
		yearMonth: {
			type: String,
			default: ()=>{
				return moment(new Date()).format('YYYY-MM')
			}
		},
		filter: {
			type: String,
			default: ()=>{
				return 'branch'
			}
		},
		bids: {
			type: Array,
			default: []
		}
	},
	data () {
		return {			
			data: [],
			ready: false,
			pageIndex: 1,
			pageSize: 10
		}
	},
	watch: {
		bids: function (val) {
			this.pageIndex = 1
			this.ready = false
			this.downCallback()
		},
		yearMonth: function (val) {
			this.ready = false
			this.pageIndex = 1
			this.downCallback()
		}
	},
	mounted () {
	},
	methods: {
		getPercent (num) {
			let result = parseFloat((num / this.total) * 100).toFixed(2)
			
			return result
		},
		loadFinished () {
			setTimeout(()=>{
				this.ready = true
			},200)
		}
	},
	computed: {
		total () {
			let data = this.data,
				total = 0
			if(data&&data.length>0){
				data.forEach(d=>{
					total += d.subtotal	
				})
			}
			return total
		},
		data_uri () {
			let uri = 'report_students/branch',
				yearMonth = moment(this.yearMonth).format('YYYYMM'),
				employee = this.user$.employee,
				bids = '',
				int_day = ''
			
			if(this.bids.length>0){
				bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
			}

			if(int_day.length===0){
				int_day = [yearMonth+'01',yearMonth+moment(this.yearMonth).daysInMonth()]
			}				
			uri = util.sprintf('report_students/%s?day=[between,%s]%s',this.filter,int_day,bids)
			return uri
		}
	}
}



